import React, {useState} from "react";
import PosterWrapper from "./posterWrapper";


const Editor = (props) => {
if (typeof window !== 'undefined') {
    const Ace = require('react-ace').default;
    require('ace-builds/src-noconflict/mode-typescript');
    require('ace-builds/src-noconflict/theme-github');

    return <Ace {...props}/>
}

return null;
}

const Header = () => {
    const [editorState, setEditorState] = useState(`
class Developer extends Person {

    public name  =  'Gherciu Gheorghe';

    protected hobby  =  'Playing tennis üèì';

    private favoriteProgrammingLanguage  =  '??? üòã';
    
    constructor() {
        return 'Hi üëã';
    };

} `)
    return (
        <div className="content-wrapper">
            <div className="header-wrapper">
                <div className="header-content">
                    <h1 className="header-heading">
                        Hi, i'm Gheorghe, <br />
                        web developer.
                    </h1>
                    <h2 className="__marketing-content">
                        <span className="content-text-subheading header-subheading">
                            My passion is creating, and I find happiness in being a better me today than the me yesterday.
                        </span>
                    </h2>
                    <a href="#works" className="button-link -thick-and-bordered">
                        View my works
                    </a>
                </div>
                <div className="header-poster">
                    <PosterWrapper>
                        <Editor
                            placeholder="Placeholder Text"
                            mode="typescript"
                            theme="github"
                            name="blah2"
                            fontSize={16}
                            showPrintMargin={false}
                            showGutter={false}
                            highlightActiveLine={false}
                            readOnly
                            value={editorState}
                            cursorStart={20}
                            height={300}
                            width={400}
                            setOptions={{
                            enableBasicAutocompletion: false,
                            enableLiveAutocompletion: false,
                            enableSnippets: false,
                            showLineNumbers: false,
                            tabSize: 4,
                        }}/>
                    </PosterWrapper>
                </div>
            </div>
            <style jsx>{`
                .header-poster {
                    position: relative;
                }
                .togglers-container {
                    display: flex;
                    align-items: center;
                    flex-direction: column;
                    position: absolute;
                    top: 0px;
                    left: -60px;
                    z-index: 9;
                    height: 100%;
                    justify-content: center;
                }
                .toggler {
                    width: 15px;
                    height: 15px;
                    border-radius: 50%;
                    margin-bottom: 10px;
                    border: 1px solid #fff;
                    position: relative;
                    cursor: pointer;
                }
                .toggler.active {
                    background: #fff;
                }
                .toggler.active:before {
                    content: "";
                    position: absolute;
                    right: -40px;
                    top: 50%;
                    transform: translateY(-50%);
                    width: 40px;
                    height: 1px;
                    background: #fff;
                }
                .header-wrapper {
                    display: flex;
                    justify-content: space-between;
                    flex-wrap: wrap;
                    margin-bottom: 200px;
                }
                .__marketing-content {
                    display: block;
                    margin-bottom: 1.5rem;
                }
                .header-heading,
                .header-subheading {
                    color: #fff;
                    max-width: 360px;
                }
                .header-heading {
                    font-weight: 500;
                }
                .header-poster img {
                    width: 100%;
                    height: 100%;
                    max-height: 300px;
                    max-width: 460px;
                }
                @media (max-width: 864px) {
                    .header-poster {
                        display: flex;
                        width: 100%;
                        margin-top: 35px;
                        justify-content: center;
                    }
                    .header-heading {
                        text-align: center;
                    }
                    .header-content {
                        margin: 10px auto;
                        display: flex;
                        align-items: center;
                        flex-direction: column;
                        width: 100%;
                    }
                    .__marketing-content {
                        width: 100%;
                    }
                    .header-subheading {
                        box-sizing: border-box;
                        width: 100%;
                        max-width: 100%;
                        text-align: center;
                    }
                }
            `}</style>
        </div>
    );
};

export default Header;
